.expertise {
  margin-top: 128px;
  padding-bottom: 105px;

  &__title {
    margin-bottom: 64px;
  }
}

.card {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;

  &__img {
    width: 184px;
    height: 166px;
    border-radius: 15px;
    margin-bottom: 40px;
    transition: transform 0.2s ease-in-out;
  }

  &__item {
    width: 264px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 15px;
    margin: 0 80px 50px;
    transition: background-color 0.2s ease-in-out,
      transform 0.3s ease-in-out,
      box-shadow 0.2s ease-in-out;

    &:hover {
      background-color: #f2f9ff;
      transform: scale(1.1);
      box-shadow: 1px 1px 10px rgba(0,0,0,0.5);

      .card__img {
        transform: scale(1.2);
        box-shadow: 0 6px 12px white;
      }
    }
  }

  &__name {
    font-weight: 600;
    font-size: 20px;
    color: $titleColor;
    margin: 16px auto;
  }

  &__description {
    font-family: "Open Sans", sans-serif;
    font-size: 14px;
    line-height: 21px;
    text-align: center;
    color: $textPassageColor;
    margin: 0;
  }
}
